import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"

import BarChart from "../images/bar-chart.svg"

function BlogPost(props) {

  // define data
  const post = props.data.markdownRemark;
  const keywords = Array.from([post.frontmatter.keywords.flat().sort()][0]);
  const date = new Date(post.frontmatter.date);
  const options = {year: 'numeric', month: 'long', day: 'numeric'};
  const dateFormated = date.toLocaleString("en-GB", options);

  // render
  return (
    <Layout>
      <main className="main" id="content" aria-label="main content">
      <header className="hero tutorial-hero">
        <div className="hero-content">
          <BarChart className="svg-image"/>
          <hr/>
          <h2>{post.frontmatter.title}</h2>
          <h1>{post.frontmatter.subtitle}</h1>
          <time>{dateFormated}</time>
        </div>
      </header>
        <article className="tutorial-post">
          <section className="tutorial-body" aria-label="post content" dangerouslySetInnerHTML={{ __html: post.html }} />
          <footer className="tutorial-footer">
          <time>{post.frontmatter.date}</time>
          <ul className="post-tags-list">
              {keywords.map((key, k) => (
                <li key={key} className="post-tag">
                  {key}
                </li>
              ))}
            </ul>
          </footer>
        </article>
      </main>
    </Layout>
  )
}

export default BlogPost
export const query = graphql`
  query PostQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        subtitle
        abstract
        date
        keywords
      }
    }
  }
`
